import React from 'react';
import "./../styles/Library.css";
import { books } from "./dbLibrary";
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    root: {
      '& > *': {
        margin: theme.spacing(1),
      },
    },
  }));
const Library = () => {

    const classes = useStyles();
    return (
        <div className="containerLibrary">
            <h3 className="subTitle">Biblioteca</h3>
            <div className="libraryBody">

                {/* <a href="./../files/Manual.pdf" download>
                    <img src={Imagen} alt="Manual de Electricista" width="104" height="142"/>
                </a> */}
                {
                    books.map((book, key) =>
                        <div className="book" key={key}>
                            <img className="bookImage"src={book.srcImg} width="104" height="142" />
                            <div>
                                <h4 className="bookTitle">{book.title}</h4>
                                <Button href={book.href} variant="outlined" color="primary" download>
                                    Descargar
                                </Button>
                            </div>
                        </div>
                    )
                }
            </div>
        </div>
    );
}

export default Library;